.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.demo-transition.transition-opacity {
  transition: opacity 0.3s ease-in;
}

.demo-transition.transition-move:after {
  transition: left 0.3s ease-in;
}

/**
 * Breakpoint Configuration
 * @see https://github.com/Team-Sass/breakpoint/wiki
 *
 * Usage:
 * display: none; // hidden for mobile
 * @include breakpoint($bp--small-up) {
 *   display: block; // shown above 400
 * }
 */
.u-grid-width--1 {
  width: calc((8.33333% + 0.16667%) - 2%) !important;
}

.u-grid-width--2 {
  width: calc((16.66667% + 0.33333%) - 2%) !important;
}

.u-grid-width--3 {
  width: calc((25% + 0.5%) - 2%) !important;
}

.u-grid-width--4 {
  width: calc((33.33333% + 0.66667%) - 2%) !important;
}

.u-grid-width--6 {
  width: calc((50% + 1%) - 2%) !important;
}

.grid-demo {
  counter-reset: grid-item;
}

.grid-demo > * {
  counter-increment: grid-item;
}

.grid-demo > *:after {
  content: counter(grid-item);
  margin-left: 3px;
}

.grid-demo > *:nth-child(odd) {
  border: dashed 1px red;
}

.grid-demo > *:nth-child(even) {
  border: dashed 1px orange;
}

.full-grid-demo {
  display: flex;
  flex-wrap: wrap;
  background: #ebebeb;
}

.full-grid-demo > * {
  margin-bottom: 2%;
  width: calc((8.33333% + 0.16667%) - 2%);
}

.full-grid-demo > *:last-child {
  margin-bottom: 2%;
}

.full-grid-demo > *:nth-child(1n) {
  margin-right: 2%;
}

.full-grid-demo > *:nth-child(12n) {
  margin-right: 0;
}

.full-grid-demo__item {
  background: black;
  border: solid 1px black;
  height: 200px;
}

.smart-grid > * {
