pre {
  margin-top: 0;
}

.sg-colors {
  overflow: hidden;
  margin: 5px;
  padding: 0;
}

.sg-colors > li {
  overflow: hidden;
  border: 1px solid #ccc;
  padding: 0.3em;
  margin: 0 0 10px;
  box-sizing: border-box;
  font-size: 12px;
}

.sg-colors > li .sg-swatch {
  display: block;
  height: 25px;
  border: dotted 1px gray;
}

.sg-colors > li .sg-info {
  line-height: 1;
}

@media all and (min-width: 400px) {
  .sg-colors > li {
    float: left;
    width: 49%;
    margin-right: 1%;
  }
}

@media all and (min-width: 700px) {
  .sg-colors > li {
    width: 24%;
  }
  .sg-colors > li:nth-child(4n+1) {
    clear: both;
  }
}

.sg-breakpoints {
  overflow: hidden;
  position: relative;
}

.sg-breakpoints ul {
  margin: 0;
  padding: 0;
}

.sg-breakpoints li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.sg-breakpoints .breakpoints > li {
  position: absolute;
  border-left: solid 3px #595959;
  height: 100%;
}

.sg-breakpoints .breakpoints > li > .label {
  display: block;
  background: #595959;
  padding: 3px;
  color: white;
}

.sg-breakpoints .breakpoints > li:hover {
  border-left-color: rgba(89, 89, 89, 0.3);
}

.sg-breakpoints .breakpoints > li:hover > .label {
  opacity: 0;
}

.sg-breakpoints .device_widths {
  padding-top: 20px;
}

.sg-breakpoints .device_widths > li {
  border-bottom: solid 3px #cccccc;
  text-align: right;
  margin-bottom: 15px;
  padding-right: 15px;
  border-right: solid 3px #cccccc;
}

.sg-font-sizes thead {
  font-weight: bold;
  text-transform: uppercase;
  border-bottom: 2px solid black;
}

.sg-font-sizes th,
.sg-font-sizes td {
  text-align: left;
  white-space: nowrap;
  line-height: 1.5em;
}

.sg-font-sizes th:first-child,
.sg-font-sizes td:first-child {
  padding-right: 12px;
}

